.logo {
  &__link {}
  &__img {}
}

.back-to-top {
  position: fixed;
  z-index: 50;
  bottom: 40px;
  right: 40px;
  display: none;
  cursor: pointer;
  animation-duration: .5s;
  animation-fill-mode: both;
  animation-name: fadeInUp;

  &__inner {
    background: $primary_color;
    border-radius: 50%;
    width: 40px;
    height: 40px;
    transition: all .3s;
    text-align: center;
    position: relative;
  }

  &__icon {
    fill: #fff;
    height: 24px;
    width: 24px;
    @include absolute-center();
  }

  &.is-active {
    display: block;
  }

  &:hover .back-to-top__inner {
    opacity: .7;
  }
}

.breadcrumb {
  background-color: transparent;
  font-size: 12px;
  font-weight: 300;
  padding: 2.5rem 0 0;

  a {
    color: #b3b3b3;
    text-decoration: underline;
  }

  & > li + li::before {
    content: "\003E";
  }

  & > li:last-child a {
    color: #000;
    text-decoration: none;
  }
}


.seller {
  font-size: 1.8rem;

  @media screen and (max-width: $break_xs) {
    text-align: center;
  }

  &__img {
    margin-bottom: 1.5rem;

    @media screen and (max-width: $bs_break_sm) {
      float: left;
      margin-right: 30px;
    }

    @media screen and (max-width: $break_xs) {
      float: none;
      margin: 0 auto 1.5rem;
      text-align: center;
    }
  }

  &__name {
    font-weight: 900;
    text-align: center;
  }

  &__post {
    text-align: center;
  }

  &__text {
    margin: 3rem 0;
  }

  a {
    color: #000;
    font-size: 1.6rem;
    text-decoration: underline;
  }
}


